<template>
	<view class="pageContent">
		<view class="pageHeaderView">
			<view class="searchView">
				<u-search placeholder="日照香炉生紫烟" bgColor="white" clearabled v-model="searchValue" :showAction="false"
					:disabled="true" @click="goSearch()">
				</u-search>
			</view>
		</view>
		<view class="swiperView">
			<u-swiper :list="swiperList" :circular="homeSwiperSetting.homeSwiperCircular"
				:indicator="homeSwiperSetting.homeSwiperIndicator" :interval="homeSwiperSetting.homeSwiperInterval"
				:autoplay="homeSwiperSetting.homeSwipreAutoplay" :showTitle="homeSwiperSetting.homwSwiperShowTitle">
			</u-swiper>
		</view>
	</view>
</template>

<script>
	import {
		mapState
	} from "vuex";
	export default {
		name: "HomePage",
		data() {
			return {
				searchValue: "我是搜索框"
			};
		},
		computed: {
			...mapState("homePage", ["homeSwiperSetting", "swiperList"])
		},
		beforeCreate() {
			console.log("页面创建了！");
			this.$store.dispatch("homePage/getHomeSwiperSet");
			this.$store.dispatch("homePage/getHomeSwiperList")
		},
		mounted() {
			// this.$store.dispatch("HomePage/getHomeSwiperSet");
			// this.$store.dispatch("HomePage/getHomeSwiperList")
		},
		methods: {
			goSearch() {
				console.log(!!this.homeSwiperSetting.homeSwiperAutoplay);
				uni.navigateTo({
					url: "/pages/SearchPage/SearchPage"
				})
			}
		}
	}
</script>

<style scoped lang="less">
	@bgc: #0dc99b;

	.pageContent {
		width: 100%;

		.pageHeaderView {
			width: 100%;
			background-color: @bgc;
			height: 100rpx;
			position: fixed;
			display: flex;
			z-index: 199999999;

			.searchView {
				width: 85%;
				margin: auto;
			}
		}

		.swiperView {
			padding-top: 100rpx;
		}
	}
</style>
